const { get, controller } = sword.Decorator;
@controller('/api/home')
class home extends sword.Controller {
    @get('/count')
    async count() {
        try {
            const data = {
                user: 132546,
                vehicle: 130045
            };
            this.success(data);
        } catch (error) {
            sword.logger.error(error);
            const json = {
                code: 202,
                data: []
            };
            this.fail(json);
        }
    }

    @get('/online')
    async online() {
        try {
            // const data = await sword.service('evidence-center.evidenceCenterDetail')(this.query);
            // if (data.errorcode === 200) {
            //     this.success(data.result);
            // } else {
            //     throw (new Error(`errorcode != 200, errorcode=${data.errorcode}`));
            // }
            const data = {
                carOnlineRate: [20, 32, 31, 34, 90, 30, 20]
            };
            this.success(data);
        } catch (error) {
            sword.logger.error(error);
            const json = {
                code: 202,
                data: []
            };
            this.fail(json);
        }
    }

    @get('/carLocation')
    async carLocation() {
        try {
            const dataArr = [];
            for (let i = 0; i < 15; i++) {
                const lat = 41 + Math.random();
                const lng = 123 + Math.random();
                const value = Math.ceil(Math.random() * 800 + 20);
                dataArr.push({
                    lat,
                    lng,
                    value
                });
            }
            const data = {
                carLocation: dataArr
            };
            this.success(data);
        } catch (error) {
            sword.logger.error(error);
            const json = {
                code: 202,
                data: []
            };
            this.fail(json);
        }
    }

    @get('./alarmInfo')
    async alarmInfo() {
        try {
            const data = {};
            this.success(data);
        } catch (error) {
            sword.logger.error(error);
            const json = {
                code: 202,
                data: []
            };
            this.fail(json);
        }
    }

    @get('./alarmTopTen')
    async alarmTopTen() {
        try {
            const data = {};
            this.success(data);
        } catch (error) {
            sword.logger.error(error);
            const json = {
                code: 202,
                data: []
            };
            this.fail(json);
        }
    }
}
export default home;
